import React from 'react';
import { Popup, Marker } from 'react-leaflet'
import * as L from 'leaflet';
import User from '../models/User';
import PropTypes from 'prop-types';

const iconsByType = {
    'police': 'police.png',
    'police-department': 'pd.png'
}

class PointOfInterest extends React.Component {
    getIconForType() {
        const { type } = this.props

        if (iconsByType[type] === undefined) {
            return
        }
        return L.icon({
            iconUrl: `${process.env.PUBLIC_URL}/assets/icons/${iconsByType[type]}`,
            iconSize: [32, 32],
            iconAnchor: [32, 32],
            popupAnchor: [-16, -16],
        });
    }
    render() {
        const icon = this.getIconForType()
        const markerOptions = {
            position: [this.props.x, this.props.y]
        }
        if (icon !== undefined) {
            markerOptions.icon = icon
        }
        return (
            <Marker {...markerOptions} >
                <Popup>
                    <p>Name: {this.props.name} </p>
                    <p>Type: {this.props.type} </p>
                    <p>Update time: {this.props.lastUpdatedAt.toString()}</p>
                    <p>Creation time: {this.props.createdAt.toString()}</p>

                    <p>Created by: {this.props.createdBy !== undefined && this.props.createdBy.toString()}</p>
                </Popup>
            </Marker >
        );
    }
}

PointOfInterest.propTypes = {
    x: PropTypes.number,
    y: PropTypes.number,
    name: PropTypes.string,
    type: PropTypes.string,
    lastUpdateAt: PropTypes.instanceOf(Date),
    createdAt: PropTypes.instanceOf(Date),
    createdBy: PropTypes.instanceOf(User)
}

export default PointOfInterest
