import React from "react";
import {LayersControl, FeatureGroup} from "react-leaflet";
import PointOfInterest from "./PointOfInterestMarker";
import FeatureLine from "./features/FeatureLine";
import FeaturePolygon from "./features/FeaturePolygon";
import FeatureCircle from "./features/FeatureCircle";

function Layer({layer}) {
    return (
        <LayersControl.Overlay checked name={layer.metadata.name}>
            <FeatureGroup>
                {layer.features.map((f, i)=> {
                    switch (f.geometry.type) {
                        case "Point":
                            if (f.properties.radius) {
                                return (<FeatureCircle key={i} feature={f} />)
                            }
                            return (
                                <PointOfInterest key={i}
                                    x={f.geometry.coordinates[0]}
                                    y={f.geometry.coordinates[1]}
                                    name={f.properties.description}
                                    type={f.properties.type}
                                    lastUpdatedAt={new Date()}
                                    createdAt={new Date()}
                                    createdBy={layer.metadata.createdBy} />
                            )
                        case "Polygon":
                            return (
                                <FeaturePolygon key={i} feature={f} />
                            )
                        case "LineString":
                            return (
                                <FeatureLine key={i} feature={f} />
                            )
                    }
                    return '';
                })}
            </FeatureGroup>

        </LayersControl.Overlay>
    )
}

export default Layer;
