import React from 'react';
import {Popup, Circle} from 'react-leaflet'
import PropTypes from 'prop-types';

function FeatureCircle({feature}) {
    const options = {}
    if (feature.properties.fill) {
        options.fillColor = feature.properties.fill;
    } else {
        if(feature.properties['marker-color']) {
            options.fillColor = feature.properties['marker-color'];
        }
    }
    if (feature.properties.stroke === false) {
        options.stroke = false;
    }
    return (
        <Circle center={feature.geometry.coordinates}
                radius={feature.properties.radius}
                pathOptions={options}>
            {feature.properties.description ?
                <Popup>
                    {feature.properties.description}
                </Popup> : ''
            }
        </Circle>
    )
}

FeatureCircle.propTypes = {
    feature: PropTypes.object,
}

export default FeatureCircle
