import './App.css';
import { MapContainer, TileLayer } from 'react-leaflet'
import * as L from 'leaflet';
import React from 'react';
import PointOfInterest from './components/PointOfInterestMarker'
import User from './models/User';
import { GestureHandling } from 'leaflet-gesture-handling';
import "leaflet-gesture-handling/dist/leaflet-gesture-handling.css";
import LocationMarker from "./components/LocationMarker";
import Layers from "./components/Layers";


class App extends React.Component {
  constructor(props) {
    super(props)
    const current = window.location.hash.length > 0 ? window.location.hash.substring(1).split(',') : null;
    this.state = {
      center: props.center || current || [51.505, -0.09],
      zoom: props.zoom || 13,
      points: props.points || [
        {
          x: 51.505,
          y: -0.09,
          name: "Object 1",
          type: "police",
          lastUpdatedAt: new Date(),
          createdAt: new Date(),
          createdBy: new User("Batman", "observer"),
        },
        {
          x: 51.505,
          y: -0.095,
          name: "Object 2",
          type: "unknown",
          lastUpdatedAt: new Date(),
          createdAt: new Date(),
          createdBy: new User("Batman", "observer"),
        },
      ]
    }
    L.Map.addInitHook('addHandler', "gestureHandling", GestureHandling);
  }
  render() {
    return (
      <div>
        <MapContainer center={this.state.center} zoom={12}>
          <TileLayer
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          {this.state.points.map((p, i) => (
            <PointOfInterest key={i} x={p.x} y={p.y} name={p.name} type={p.type} lastUpdatedAt={p.lastUpdatedAt}
              createdAt={p.createdAt} createdBy={p.createdBy} />
          ))}
          <Layers />
          <LocationMarker />
        </MapContainer>
      </div>
    );
  }
}

export default App;
