import React, {useEffect} from "react";
import { Popup, LayersControl, Circle, LayerGroup, FeatureGroup, Rectangle } from "react-leaflet";
import {useState} from "react";
import {getData} from "../models/Layer";
import Layer from "./Layer";

function Layers() {
    const [layers, setLayers] = useState([])
    const center = [51.505, -0.09]
    const rectangle = [
        [51.49, -0.08],
        [51.5, -0.06],
    ]
    useEffect(() => {
       getData().then(data => {
           setLayers(data)
       })
    }, []);

    return (
        <LayersControl position="topright">
            {layers.map(l => (
              <Layer key={l.metadata.name} layer={l} />
            ))}
        </LayersControl>
    )
}

export default Layers;
