import React from 'react';
import {Popup, Polyline} from 'react-leaflet'
import PropTypes from 'prop-types';

function FeatureLine({feature}) {
    const pathOptions = {weight: feature.properties['stroke-width'], color: feature.properties.stroke};
    return (
        <Polyline pathOptions={pathOptions} positions={feature.geometry.coordinates}>
            {feature.properties.description ?
                <Popup>
                    {feature.properties.description}
                </Popup> : ''
            }
        </Polyline>
    );
}

FeatureLine.propTypes = {
    feature: PropTypes.object,
}

export default FeatureLine
